      height: 80px;
      width: 80px;
      margin: 8.5px 16px;
    }
  
    .cart-card-photo {
      height: 66px;
      width: 66px;
      margin: 7px;
    }
  
    .cart-card-nameBox {
      width: 128px;
      margin: 0;
    }
  
    .cart-card-name {
      align-self: start;
      list-style: none;
      line-height: 21px;
      font-size: 14px;
