      border: solid 1px #b4bdc3;
      border-radius: 48px;
    }
  
    .cart-card-amount-icon {
      width: 16px;
      height: 16px;
      margin: 8px;
    }
  
    .cart-card-amount-numBox {
      width: 32px;
      height: 32px;
      justify-content: center;
      display: flex;
      align-items: center;
    }
  
    .cart-card-amount-num {
      text-align: center;
      width: 32px;
      font-size: 14px;
      line-height: 21px;
      font-weight: 600;
      margin: auto;
    }
  
    .cart-card-priceBox {
      width: 51px;
      height: 31px;
      display: flex;
      align-items: center;
      margin: 0;
    }
  
    .cart-card-price {
      font-weight: 800;
      font-size: 22px;
      justify-self: end;
    }
  }